import config from './config.js'
import Http from './request.js'

/* const BaseUrl = config.baseUrl + '/user/api'  */
const BaseUrl = config.baseUrl + '/api/v1'
/**
 * api,
 */
export const ApiFormat = async (fun) => {
	try {
		const result = await fun
		return {
			result: result,
			error: null
		}
	} catch (error) {
		return {
			result: null,
			error: error
		}
	}
}

/**
 * apiloading,,
 */
export const ApiBase = async (fun, {
	showLoading = false,
	loadingText = '',
	showError = true
} = {}) => {

	// loadingKey
	const loadingKey = Math.random().toString(36).slice(-8) + new Date().getTime()
	try {
		if (showLoading) uni.showLoading({
			title: loadingText
		})

		// 
		const {
			result,
			error
		} = await ApiFormat(fun)
		showLoading && uni.hideLoading()
		if (error && showError) {
			uni.showToast({
				title: error.message || '',
				duration: 2000,
				icon: 'none'
			})
			return {
				result,
				error
			}
		}

		// if (result.code != 0 && showError) {
		//   message.error({ content: result.msg || '', key: loadingKey })
		//   return { result, error } as ApiResult
		// }
		return {
			result,
			error
		}
	} catch (error) {
		if (showError) {
			uni.showToast({
				title: error.message || '',
				duration: 2000,
				icon: 'none'
			})
		} else {
			if (loading) loading();
			loading = null
		}
		return {
			result: null,
			error
		}

	}
}

//
export const errorCheck = (result, error) => {
	if (!result || result.data.code !== 0) {
		if ([-1, -2, -3, -4, -5, 1610].indexOf(result.data.code) !== -1) {
			uni.showToast({
				title: '',
				duration: 2000,
				icon: 'none'
			})
			uni.reLaunch({
				url: '/pages/login/index'
			});
			return false;
		}
		uni.showToast({
			title: result.data.msg,
			duration: 2000,
			icon: 'none'
		})
		return false;
	}
	return true;
}

