/**
 * KeyValue
 * @param {*} key
 * @param {*} defVal 
 */
const getValueByKey = (data, key, defVal) => {
	for (let i = 0; i < data.length; i++) {
		const item = data[i]
		if (item.key == key) {
			return item.value
		}
	}
	return defVal || ''
}
// ValueKey
const getKeyByValue = (data, value, defVal) => {
	for (let i = 0; i < data.length; i++) {
		const item = data[i]
		if (item.value == value) {
			return item.key
		}
	}
	return defVal || ''
}

// 
const durationFormat = (duration) => {
	let time = "";
	// 
	if (Math.floor(duration / 86400) > 0) {
		time = time + Math.floor(duration / 86400) + ""
	}
	// 
	if (Math.floor((duration % 86400) / 3600) > 0) {
		time = time + Math.floor((duration % 86400) / 3600) + ""
	}
	// 
	if ((Math.floor(((duration % 86400) % 3600) / 60)) > 0) {
		time = time + (Math.floor(((duration % 86400) % 3600) / 60)) + ""
	}
	// 
	if ((((duration % 86400) % 3600) % 60) > 0) {
		time = time + (((duration % 86400) % 3600) % 60) + ""
	}
	return time;
}

// timeZEROformat 0
const timeZeroFormat = (time) => {
	if (time && time != "1970-01-01 08:00:00") {
		return time
	}
	return ''
}

